
/* Header.js */
.Kullylogo{
  text-align: center;
  padding: 40px 0 40px;
}

.topspace{
  border: none;
  border-top:2px solid#ccc;
  margin: 0 auto;
  width: 1200px;
}

/* Main.js */
.md_h2{
  color: #333;
  text-align: center;
  padding: 60px 0 20px;
}

.md_tap a.act{background:#5F0080 !important; color:#fff;}
.md_tap{
  width: 800px;
  margin: 0 auto;
  text-align: center;
}

.md_tap ul li,
.md_tap ol li {
  list-style: none !important;
  margin: 0;
  padding: 0;
}
.md_tap a{
  background:rgba(189, 189, 189, 0.5);
  color:#333;
  text-decoration:none;
  font-size:13px;
  border-radius:15px;
  padding:4px 10px;
}
.md_tap ul{
  display:flex;
  justify-content: space-around;
}
.md_tap > div {
  display: block;         /* 기본값이지만, 명시적으로 선언 */
  line-height: 30px;
  margin-bottom: 10px;    /* 각 div 사이 간격 */
}

/* 상품 리스트 (기존 캐러셀 → ul/ li 구조로 변경)        */
.carousel-list {
  display: flex;
  justify-content: center;  /* ← 아이템들을 가로축 가운데로 */
  gap: 16px;
  padding: 0;
  margin: 70px auto 60px;
  list-style: none;
}
.carousel-list::-webkit-scrollbar {
  display: none;
}

.carousel-item {
  flex: 0 0 auto;
  width: 240px;
  overflow: hidden;    /* 자식(img)의 확대 시 넘침 차단 */
  border-radius: 0;    /* 둥근 모서리 제거 */
}

.carousel-item a {
  display: block;
  text-decoration: none;
  color: inherit;
}

.carousel-item img {
  width: 100%;
  display: block;
  /* border-radius 삭제 → 사각형 유지 */
  /* transition 유지 */
  transition: transform 0.2s;
}
.carousel-item img:hover {
  transform: scale(1.1);
}

/* 정보 영역 */
.carousel-item .info {
  width: 100%;
  box-sizing: border-box;
  margin: 8px 0 0 0;   /* 위쪽만 8px 띄우고, 좌우·아래는 0 */
  padding: 0;          /* 모든 패딩 제거 */
}

.carousel-item .title {
  font-size: 14px;
  line-height: 1.3;
  margin: 0;            /* 기본 마진 제거 */
  padding-left: 0;      /* 왼쪽 패딩 제거 */
  color: #333;
}

.carousel-item .pricing {
  font-size: 14px;
  display: flex;
  align-items: baseline;
  gap: 6px;
  padding: 0;
}

.carousel-item .discount {
  color: #ff5722;
  font-weight: bold;
  padding: 0;
}

.carousel-item .current {
  color: #333;
  font-weight: bold;
  padding: 0;
}

.carousel-item .original {
  font-size: 13px;
  color: #999;
  text-decoration: line-through;
  margin: 0;
  padding: 0;
}

.image-wrapper {
  position: relative;
  overflow: hidden;
}

.image-wrapper img {
  width: 100%;
  display: block;
}

.cart-icon {
  position: absolute;
  bottom: 8px;
  right: 8px;

  width: 32px;               /* 가로 32px */
  height: 32px;              /* 세로 32px */
  display: flex;             /* 내부 아이콘을 가운데 정렬하기 위함 */
  align-items: center;
  justify-content: center;

  background: rgba(95, 0, 128, 0.8);
  border-radius: 50%;        /* 완전한 원(circle) */
  color: #fff;

  transition: transform 0.2s;
}

.cart-icon:hover {
  transform: scale(1.1);
}


/* 랜덤배너 */
.ran_banner {
  width: 1200px;
  overflow: hidden;
  /* 필요하다면 높이 고정 */
  /* height: 200px; */
  margin: 0 auto;
}

.ran_banner img {
  display: block;   /* 이미지 아래 공백 제거 */
  width: 100%;      /* 컨테이너에 꽉 채움 */
  height: auto;     /* 비율 유지 */
  object-fit: cover;/* 크롭하고 싶다면 */
}

/* Footer.js */
footer{background: #eeeeee;}
footer > address{
  font-style:normal;
  text-align: center;
  line-height: 50px;
  font-size: 12px;
}