* {margin: 0;padding: 0;}

body{
  /* padding: 10px; */
  line-height: 30px;
  color: #333;
  font-size: 13px;
}
/* .App {
  background:#ffff00;
} */
.App > h2{
  color: #ff0000;
  text-align: center;
}

.header{
  text-align: center;
  width: 100%;
  height: 60px;background: rgb(23, 104, 255);
  position: fixed;
  top: 0px;left: 0px;
  box-shadow: 0px 0px 10px #969696;
  z-index: 10001;
  color: #fff;
  margin-bottom: 60px;
}
.header h1{
  margin-top: 12px;
}

main{
  padding: 30px 0;
}

.title3{
  color: #4BADFD;
  border-top: 3px solid #4BADFD;
  border-bottom: 3px solid #4BADFD;
  margin-top: 30px;
  padding: 10px 0px;
}


/* ul{margin-left: 14px;} */

ul {
  list-style: disc;
  padding-left: 30px;
}

ol {
  list-style: decimal;
  padding-left: 30px;
}

li {
  display: list-item; /* 기본값이지만 명시해주면 안전함 */
}

.footer{
  background: #333;
  color: #fff;
  text-align: center;
  line-height: 40px;
  margin-top: 40px;
}